import VueEditPlugin from './edit.vue'
const editPlugin = {
  install: function(Vue) {
    Vue.component(VueEditPlugin.name, VueEditPlugin)
  }
}
// global  
if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.use(editPlugin)
}
// 
export default editPlugin



